                            <h5> Bipartición </h5>
                        </div>
                        <div class="card-body lesson">
                            <p>
                                Para poder comenzar a conocer el significado del término bipartición, se hace necesario
                                proceder a descubrir, en primer lugar, su origen etimológico. En este caso, podemos
                                establecer que es una palabra que deriva del latín, exactamente de “bipartitio”, que
                                puede traducirse como la acción de partir en pedazos. Asimismo, hay que subrayar que es
                                fruto de la suma de tres componentes diferenciados:
                                -El prefijo “bi-”, que es equivalente a “dos”.
                                -El verbo “partiri”, que es sinónimo de “dividir”.
                                -El sufijo “-cion”, que se utiliza para indicar “acción y efecto”.

                            </p>
                            <p>
                                Se trata de la segmentación de alguna cosa en dos fracciones o porciones.

                                El concepto suele utilizarse con referencia a un mecanismo de reproducción asexual que
                                también se conoce como fisión binaria. La bipartición, en este sentido, es desarrollada
                                por ciertos protozoos, levaduras, algas y bacterias que duplican su ADN y luego dividen
                                el citoplasma, obteniendo como resultado dos células.'
                                <br>
                                <br>
                                <br>
                            </p>
                            <center>
                                <img :src="getbigimgpath('n3l2_img2.png')" class="img-thumbnail img-cropper">
                            </center>
                            <br>
                            <br>
                            <br>
                            <p>
                                La mayoría de las bacterias desarrollan su reproducción a través de la bipartición. Por
                                eso, en ciertos contextos, proliferan con gran rapidez. En pocos minutos, de una
                                bacteria pueden surgir dos y así crece la población de manera exponencial.
                            </p>
                            <p>
                                Existen diversos tipos de bipartición. <b>La bipartición regular implica</b> la división
                                simétrica de la célula, generándose dos células hijas de idéntico tamaño. La bipartición
                                transversal, por su parte, hace que el citoplasma se divida de manera perpendicular
                                respecto al eje del huso.

                                <b>La bipartición longitudinal</b>, como su nombre lo indica, se desarrolla longitudinalmente
                                al eje de división. También existe la <b>bipartición oblicua</b>. La bipartición tipo ameba,
                                por último, es perpendicular en cuanto al eje, pero irregular respecto al citoplasma.
                            </p>
