import Vue from 'vue'
import Router from "vue-router";

import Body from '../components/website/body'

/* sample page */
import SamplePage from '../pages/sample_page.vue'
import Leve1Lesson1 from '../pages/level1_lesson1'
import Leve2Lesson1 from '../pages/level2_lesson1.vue'
import Leve2Exam from '../pages/level2_exam.vue'
import Leve3Exam from '../pages/level3_exam.vue'
import Leve3Lesson1 from '../pages/level3_lesson1.vue'
import Leve3Lesson2 from '../pages/level3_lesson2.vue'
import Leve3Lesson3 from '../pages/level3_lesson3.vue'
import Leve3Lesson4 from '../pages/level3_lesson4.vue'
import Leve3Lesson5 from '../pages/level3_lesson5.vue'
import Leve4Lesson1 from '../pages/level4_lesson1.vue'
import Leve4Lesson2 from '../pages/level4_lesson2.vue'
import Leve4Lesson3 from '../pages/level4_lesson3.vue'
import Leve4Exam from '../pages/level4_exam.vue'
// component

Vue.use(Router)

const routes = [
   {path: '', redirect: {name: 'default'}},
   {
      path: '/dashboard',
      component: Body,
      children: [
         {
            path: 'default',
            name: 'default',
            component: SamplePage,
            meta: {
               title: 'Biología 8º  | Reproducción en los seres vivos',
            }
         },
         {
            path: 'nivel1/leccion1',
            name: 'Nivel 1 - Exploración de saberes',
            component: Leve1Lesson1,
            meta: {
               title: 'Biología 8º  | Nivel 1. Exploración de saberes ',
            }
         },
         {
            path: 'nivel2/leccion1',
            name: 'Nivel 2',
            component: Leve2Lesson1,
            meta: {
               title: 'Biología 8º  | Nivel 2. Concepto de reproducción ',
            }
         },{
            path: 'nivel2/exam',
            name: 'Nivel 2',
            component: Leve2Exam,
            meta: {
               title: 'Biología 8º  | Nivel 2. Concepto de reproducción ',
            }
         },
         {
            path: 'nivel3/leccion1',
            name: 'Nivel 3',
            component: Leve3Lesson1,
            meta: {
               title: 'Biología 8º  | Nivel 3. Reproducción axesual y sus clases ',
            }
         },
         {
            path: 'nivel3/leccion2',
            name: 'Nivel 3',
            component: Leve3Lesson2,
            meta: {
               title: 'Biología 8º  | Nivel 3. Reproducción axesual y sus clases  ',
            }
         },
         {
            path: 'nivel3/leccion3',
            name: 'Nivel 3',
            component: Leve3Lesson3,
            meta: {
               title: 'Biología 8º  | Nivel 3. Reproducción axesual y sus clases  ',
            }
         },
         {
            path: 'nivel3/leccion4',
            name: 'Nivel 3',
            component: Leve3Lesson4,
            meta: {
               title: 'Biología 8º  | Nivel 3. Reproducción axesual y sus clases  ',
            }
         },
         {
            path: 'nivel3/leccion5',
            name: 'Nivel 3',
            component: Leve3Lesson5,
            meta: {
               title: 'Biología 8º  | Nivel 3. Reproducción axesual y sus clases  ',
            }
         },
         {
            path: 'nivel3/exam',
            name: 'Nivel 3',
            component: Leve3Exam,
            meta: {
               title: 'Biología 8º  | Nivel 2. Concepto de reproducción ',
            }
         },
         {
            path: 'nivel4/leccion1',
            name: 'Nivel 4',
            component: Leve4Lesson1,
            meta: {
               title: 'Biología 8º  | Nivel 4. Reproducción sexual y fecundación',
            }
         },
         {
            path: 'nivel4/leccion2',
            name: 'Nivel 4',
            component: Leve4Lesson2,
            meta: {
               title: 'Biología 8º  | Nivel 4. Reproducción sexual y fecundación ',
            }
         },
         {
            path: 'nivel4/leccion3',
            name: 'Nivel 4',
            component: Leve4Lesson3,
            meta: {
               title: 'Biología 8º  | Nivel 4. Reproducción sexual y fecundación ',
            }
         },{
            path: 'nivel4/exam',
            name: 'Nivel 4',
            component: Leve4Exam,
            meta: {
               title: 'Biología 8º  | Nivel 4. Reproducción sexual y fecundación ',
            }
         }
         
      ]
   }];


const router = new Router({
   routes,
   base: '/',
   mode: 'history',
   linkActiveClass: "active",
   scrollBehavior() {
      return {x: 0, y: 0}
   }
});

export default router
