                    <div class="card">
                        <div class="card-header">
                            <h5> Gemación </h5>
                        </div>
                        <div class="card-body lesson">
                            <p>
                                Para poder descubrir el significado del término gemación se hace necesario, en primer
                                lugar, conocer su origen etimológico. En este caso, podemos subrayar que es una palabra
                                que deriva del latín, exactamente de “gemmatio” que significa “formación de una yema” y
                                que es fruto de la suma de varios componentes léxicos de dicha lengua:
                                -El sustantivo “gemma”, que es sinónimo de “yema”.
                                -El sufijo “-cion”, que se utiliza para indicar lo que es “acción y efecto”.

                            </p>
                            <p>
                                Alude a un método de reproducción asexual de vegetales y animales invertebrados. La
                                gemación consiste en la separación de un fragmento de un individuo, que va
                                desarrollándose hasta generar un ejemplar semejante al original.
                                <br>
                                <br>
                                <br>
                            </p>
                            <center>
                                <img :src="getbigimgpath('n3l3_img3.png')" class="img-thumbnail img-cropper">
                            </center>
                            <br>
                            <br>
                            <br>
                            <p>
                                La gemación se produce por división celular. Mientras el nuevo organismo crece, se
                                mantiene unido a su progenitor, hasta que alcanza la madurez y se separa. Es importante
                                mencionar que el progenitor y el nuevo ejemplar comparten el código genético, ya que el
                                primero se lo transmite al segundo.
                            </p>
                            <p>
                                Además de todo lo expuesto no podemos pasar por alto tampoco que existen dos tipos de
                                gemaciones fundamentalmente:
                            </p>
                            <p>
                                <b>-La gemación de supervivencia. </b> Esta, como su propio nombre, es la que llevan a cabo los
                                organismos cuando descubren que se están dando una serie de condiciones desfavorables
                                que pueden ponerles en serio peligro. Entonces, ante esa situación, en pro de
                                sobrevivir, optan por incrementar su número, es decir, la cantidad de descendientes.
                                Este tipo de gemación puede encontrarse, por ejemplo, en los pólipos de los corales.
                            </p>
                            <p>
                                <b>-La gemación de propagación. </b> Este segundo tipo de gemación es el que tiene lugar cuando
                                los organismos viven con unas condiciones medioambientales inmejorables y favorecedoras.
                                En ese caso, lo que hacen es apostar por comenzar a multiplicarse mediante la gemación
                                por dos principales motivos: para aprovechar y sacarle el máximo partido a los recursos
                                que tienen y para aumentar su número de descendientes.
                                <br>
                                <br>
                                <br>
                            </p>
                            <center>
                                <img :src="getbigimgpath('n3l3_img2.png')" class="img-thumbnail img-cropper">
                            </center>
                            <p>
                                <br>
                                <br>
                                <br>
                                Los corales, ciertas lombrices y varias esponjas se reproducen a partir de la gemación
                                de la madre. Más allá de estos animales pluricelulares, la gemación también se da como
                                mitosis asimétrica en organismos unicelulares, consistiendo en la aparición de una yema
                                (un abultamiento) en la membrana celular o plasmática.
                            </p>

