                                La reproducción sexual es el proceso por el cual se forma un individuo a partir de la
                                unión de dos progenitores o padres: el macho y la hembra. Cada progenitor produce
                                células sexuales llamadas gametos. El gameto del macho o masculino, por lo general se
                                llama espermatozoide y el gameto femenino es el óvulo. Cuando se unen el óvulo y el
                                espermatozoide, se forma un huevo o cigoto que originará un nuevo ser que conservará
